import Article from "@/components/article/Article.vue";

export default {
    name: 'Related',
    components: {
        Article
    },
    data: () => ({
        search: ''
    }),
    props: {
        scrolled: Number,
        bgHeight: Number
    },
    async beforeRouteUpdate(to, from, next) {
        const { asyncData } = this.$options;
        if (asyncData) {
            try {
                // this.$nprogress.start();
                this.$data.search = '';
                await asyncData({ store: this.$store, route: to });
                // this.$nprogress.done();
            } catch (error) {
                next(error);
            }
        } else {
            next();
        }
    },
    computed: {
        format: function() {
            return this.$store.state.global.hexoConfig.dateTimeFormat.date_format;
        },
        posts: function() {
            const { type } = this.$route.params;
            const rootState = this.$store.state;
            if (type === 'category') {
                return rootState.categories.oneCategoryPosts.postlist;
            } else if (type === 'tag') {
                return rootState.tags.oneTagPosts.postlist;
            }
            return [];
        },
        displayPosts: function() {
            const search = this.$data.search;
            return search.length ? this.posts.filter(post => post.title.toLowerCase().indexOf(search.toLowerCase()) !== -1) : this.posts;
        },
        slug: function() {
            return this.$route.params.slug;
        }

    },
    async fetch({ store, route }) {
        const { type, slug } = route.params;
        if (type === 'category') {
            await store.dispatch('categories/fetchRelatedPostsOfCategory', { slug });
        } else if (type === 'tag') {
            await store.dispatch('tags/fetchRelatedPostsOfTag', { slug });
        }
    }
}